.NavBar{
    float: left;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;

}

.NavBar .navEntry{
    width: 100%;
    height: content-box;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 5% 5% 5% 15%;
    text-decoration: none;
}
.NavBar .navText{
    float: right;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    color: black;
    text-decoration: none;
    font-size: 2vh;
    margin-left: 5%;
    height: 100%;
    font-family: 'Poppins', sans-serif;
}
.NavBar .active{
    background-color: #e4f0ef;
}
.NavBar :hover{
    background-color: #e4f0ef;
}
.navLi{
    color: black;
    text-align: center;
    list-style: none;
    padding-left: 10%;
    width: 100%;
    height: 10%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.navLi:hover{
    background-color: cornflowerblue
}

.navLi:active{
    background-color: cornflowerblue
}

.navA{

}


.navA:hover{

}




@media (pointer:none), (pointer:coarse) {
    .NavBar{
        position: fixed;
        top: 10%;
        right: 0;
        background-color: cadetblue;
        display: none;
        flex-direction: row;
        align-items: center;
        align-content: center;
        margin-right: 0%;
        list-style-type: none;
    }
    .navLi{
        display: block;
        margin-bottom: 10%;
        color: white;
        text-align: center;
        list-style: none;
        width: 100%;




    }

    Link{
        display: block;
        color: white;
        text-decoration: none;
        font-size: 4vh;
        font-family: 'Acme', sans-serif;
    }
    a:hover{
        color: grey;
    }
}