#StationDetailsFragment{
    display: block;
    height: auto;
}

.StationDetailsFragment .globalInformationHolder{
    float: left;
    width: 100%;
    background-color: white;
    box-shadow: 10px 10px 5px #aaaaaa;
    padding: 2%;
    display: flex;
    justify-content: space-around;
    margin-bottom: 10px;
}
.StationDetailsFragment .informationHolder{
    float: right;
    background-color: white;
    box-shadow: black;
    width: 100%;
    box-shadow: 10px 10px 5px #aaaaaa;
    display: block;
    flex-direction: column;
    justify-content: space-around;
    padding: 1%;
    margin-bottom: 20px;
}

.infoSection{
    float: left;
    width: 100%;
    padding-bottom: 2%;
}


