.TransactionsPage{
    width: 100%;
    height: 100%;
    padding: 3%;
    display: block;
    overflow: auto;
}

.TransactionsPageContent{
padding: 0%;
}

.TransactionsPage .StatsContainer{

    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 20%;
    margin-bottom: 20px;
}

.TransactionsPage .TableHeader{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 10px;
    border-bottom: 1px solid grey;
    font-family: Poppins;
}

.tableHeaderTabsContainer{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: space-around;
}
.tableTab{
    cursor: pointer;
}
