.Home{
    width: 100%;
    height: 100%;
    padding: 3%;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: space-around;
    justify-items: center;
    justify-content: space-around;
    overflow: auto;
}

        .StatsContainer{
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            height: 20%;
            margin-bottom: 1%;
        }

        .StatsContainer .container{
            width: 23%;
            height: 100%;
        }

        .transactionsContainer{
            padding: 1%;
            float: left;
            background-color: white;
            box-shadow: 5px 5px 5px gray;
            height: 80%;
            width: 63%;
            overflow: auto;
        }

        .Home .quickSettingsContainer{
            overflow: auto;
            background-color: #ffffff;
            box-shadow: 5px 5px 5px gray;
            float: right;
            width: 35%;
            height: 80%;
        }
        .sexCharContainer{
            margin-top: 10px;

            float: left;
            width: 400px;
            height: 300px;
        }

        .earningStatHolder{
            float: right;
            width: 40%;
            margin-top: 10px;
            height: 200px;
        }

@media (pointer:none), (pointer:coarse) {
    .homeLogo{
        display: none;
    }

    .homeText{
        display: none;
        color: white;
        font-size: 3vh;

    }

    .homeLogoContainer {
        display: block;
    }

}