.active{
    position: relative;
}

.anActive{
    border-radius: 20px;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.7);
    height: 98%;
    width: 98%;
    left: 1%;
    top: 1%;
    color: #efefef;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}