.YitPage{
    height: 100%;
    width: 100%;
}

.YitPageMainPage{
    width: 100%;
    height: 100%;
    display: block;
    padding: 3%;
}

.mainPageContent{
    float: left;
    height: 90%;
    width: 100%;
}

.pagesTitle{
    height: 10%;
    float: left;
    width: 50%;
    padding-left: 2%;
    border-left: 5px solid darkblue;
    display: flex;
    align-items: center;
    margin-bottom: 2vh;
    font-size: 2.5vw;
    font-family: 'Poppins', sans-serif;
}

.pagesOperations{
    width: 20%;
    height: 10%;
    float: right;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: relative;
    flex-direction: row;
}

.entriesContainer{
    visibility: hidden;
    display: flex;
    flex-direction: row;
    justify-items: right;
    justify-content: right;
    margin-right: 20px;
    font-size: 1.4vw;
    font-family: 'Acme', sans-serif;
}
.operationEntry{
    cursor: pointer;
}


.YitPage .YitPageBackDrop{
    padding: 5%;
    display: none;
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.7);
    align-items: center;
    justify-content: center;
    justify-items: center;
    align-items: center;
    justify-content: center;

}


.YitPage .YitPageBackDropContent{
    padding: 0;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
    justify-items: center;
    align-items: center;
    justify-content: center;
    width: auto;
    height: auto;
}

.loadingPage{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.failedToLoadPage{
    font-size: 1.5vw;
    color: #FFFFFF;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

