.YitFragment{
    width: 100%;
    height: 100%;
}

.mainPageContent{
    float: left;
    height: 90%;
    width: 100%;
}

.BackDrop{
    padding: 5%;
    display: none;
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.5);
    align-items: center;
    justify-content: center;
    justify-items: center;
    align-items: center;
    justify-content: center;

}

.BackDrop .close{
    cursor: pointer;
    position: absolute;
    top: 5%;
   right: 5%;
    color: red;
}

.BackDrop .content{
    padding: 0;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
    justify-items: center;
    align-items: center;
    justify-content: center;
    width: auto;
    height: auto;
}

.loadingPage{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.failedToLoadPage{
    font-size: 1.5vw;
    color: #FFFFFF;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}