.StatBox{
    position: relative;
    background-color: white;
    width: 100%;
    border-radius: 10px;
    height: 100%;
    padding: 5%;
}

    .IconHolder{
        float: left;
        width: 29%;
        display: flex;
        align-items: center;
        height: 70%;
        width: 30%;
        justify-content: center;
        top: -20px;
        left: 20px;

    }
    .statsHolder{
        float: right;
        display: flex;
        height: 50%;
        flex-direction: column;
        justify-content: space-around;
        align-items: flex-end;
        font-size: 1.5em;
        padding-right: 5%;
        padding-left: 5%;
    }
        .statsHolder .title{
            font-family: Acme;
            color: #54667a;
            font-size: 1.5vw;
        }

        .alt{
            float: left;
            padding: 5%;
            width: 100%;
            border-top: 2px solid grey;
            font-size: 1vw;
            color: #282c34;
            font-family: -apple-system;
        }